import * as React from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { Tilt } from 'react-next-tilt';
import './index.css';


const getRandomInt = (min, max) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  // const posts = data.allMarkdownRemark.nodes
  const styleImg = {
    width: '400px',
    height: '560px',
  }
  console.log("随机一卡")
  const randomIndex = getRandomInt(0, posts.length - 1);
  const url = `/cards/${posts[randomIndex].frontmatter.id}.png`;
  const jump_url = `/${posts[randomIndex].frontmatter.id}`;
  return (
    <Layout location={location} title={siteTitle} >
      <div>
        <h2>随机一卡</h2>
        <a href={jump_url}>
          <Tilt style={styleImg} scale={1.0} shadowEnable={true} lineGlareEnable={false} spotGlarePosition="all" borderRadius="18px">
            <img src={url} style={styleImg} />
          </Tilt>
        </a>
      </div>


    </Layout>
  )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="All posts" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: { frontmatter: { type: { eq: "card" } } }
      sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          id
        }
      }
    }
  }
`
